@media ( max-width:480px )

  .button
    font-size: 16px

  .container
    padding-left: 30px
    padding-right: 30px

  .main-slider__container
    padding-top: 144px
  .main-slider
    max-height: 489px
  .main-slider__head
    max-width: 76%

  .main-about__head
    margin-bottom: 22px
  .main-about__spec
    padding-top: 20px
  .main-about__spec-icon
    width: 50px
    height: 50px
    &--chair
      width: 26px
    &--wardrobe
      width: 26px
    &--home
      width: 26px
  .main-about__spec-title
    font-size: 13px

  .mini-slider
    padding-bottom: 25px
  .mini-slider__container
    padding-left: 0
    padding-right: 0
  .mini-slider__slider
    height: 280px
  .mini-slider__intro
    width: 140px
    top: 100px
  .mini-slider__button
    margin-top: 79px

  .footer__content
    grid-template-rows: 40px 1fr
  .footer__phone,
  .footer__email,
  .footer__adress
    font-size: 14px
  .footer__copyright
    font-size: 13px
